  public generaGrafico(id:string,valorSi:number,valorNo:number,colorSi:string,colorNo:string){
    var chartDom = document.getElementById(id);
    var myChart = echarts.init(chartDom);
    var option:any;

    option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [
        {
          type: 'category',
          data: ['Si', 'No'],
          axisTick: {
            alignWithLabel: true
          }
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      series: [
        {
          name: 'Direct',
          type: 'bar',
          barWidth: '60%',
          data: [{value: valorSi,itemStyle: {color: colorSi}},{value: valorNo,itemStyle: {color: colorNo}}],
          animationDelay: function(idx:any) {
            return idx * 600 + 100;
          }
        }
      ]
    };

    option && myChart.setOption(option);
  }



  


